import { Card, CardActions, CardHeader, CardText, CardTitle, FlatButton, RaisedButton } from 'material-ui';
import { Col, Grid, Row } from 'react-bootstrap';
import {
  Table,
  TableBody,
  TableHeader,
  TableHeaderColumn,
  TableRow,
  TableRowColumn,
} from 'material-ui/Table';
import { VerticalCanvas, FullPageCanvas, GlassCard, DynamicSpacer } from 'meteor/clinical:glass-ui';
import { get, has } from 'lodash';

import { ObservationsTable } from 'meteor/clinical:hl7-resource-observation';
import { AllergyIntolerancesTable } from 'meteor/clinical:hl7-resource-allergy-intolerance';
import { ConditionsTable } from 'meteor/clinical:hl7-resource-condition';
import { ImmunizationsTable } from 'meteor/clinical:hl7-resource-immunization';
import { CarePlansTable } from 'meteor/clinical:hl7-resource-careplan';
import { MedicationStatementsTable } from 'meteor/clinical:hl7-resource-medication-statement';
import { MedicationOrdersTable } from 'meteor/clinical:hl7-resource-medication-order';
import { DiagnosticReportsTable } from 'meteor/clinical:hl7-resource-diagnostic-report';
import { PatientsTable } from 'meteor/clinical:hl7-resource-patient';
import { ProceduresTable } from 'meteor/clinical:hl7-resource-procedure';

import MedicalRecordImporter from '../lib/MedicalRecordImporter.js'
// import ImagingStudiesTable from '/imports/ui/workflows/imaging-studies/ImagingStudiesTable';

import Avatar from 'material-ui/Avatar';

import React from 'react';
import { ReactMeteorData } from 'meteor/react-meteor-data';
import ReactMixin from 'react-mixin';

import { browserHistory } from 'react-router';
// import ReactGA from 'react-ga';

var callback = function (msg) {
  // wrapping in a timeout because of a possbile native UI element blocking the webview
  setTimeout(function () {
    // alert(JSON.stringify(msg))
    console.log(JSON.stringify(msg))
  }, 0);
};


Session.set('useMinimongoCache', true);


export class ContinuityOfCarePage extends React.Component {
  constructor(props) {
    super(props);
  }
  getMeteorData() {
    let data = {
      style: {},
      ccd: {
        allergyIntolerances: [],
        careplans: [],
        conditions: [],
        diagnosticReports: [],
        immunizations: [],
        medications: [],
        medicationOrders: [],
        medicationStatements: [],
        observations: [],
        patients: [],
        procedures: []
      },
      minimongo: {
        allergyIntolerances: [],
        careplans: [],
        conditions: [],
        diagnosticReports: [],
        immunizations: [],
        medications: [],
        medicationOrders: [],
        medicationStatements: [],
        observations: [],
        patients: [],
        procedures: []
      },
      query: {}
    };
    
    if(Meteor.user()){
      if(typeof AllergyIntolerances === "object"){
        data.ccd.allergyIntolerances = AllergyIntolerances.find().fetch();
      }
      if(typeof CarePlans === "object"){
        data.ccd.carePlans = CarePlans.find().fetch();
      }
      if(typeof Conditions === "object"){
        data.ccd.conditions = Conditions.find().fetch();
      }
      if(typeof DiagnosticReports === "object"){
        data.ccd.diagnosticReports = DiagnosticReports.find().fetch();
      }
      if(typeof Immunizations === "object"){
        data.ccd.immunizations = Immunizations.find().fetch();
      }
      if(typeof Medications === "object"){
        data.ccd.medications = Medications.find().fetch();
      }
      if(typeof Medications === "object"){
        data.ccd.medicationOrders = MedicationOrders.find().fetch();
      }        
      if(typeof MedicationStatements === "object"){
        data.ccd.medicationStatements = MedicationStatements.find().fetch();
      }
      if(typeof Patients === "object"){
        data.ccd.patients = Patients.find().fetch();
      }
      if(typeof Observations === "object"){
        data.ccd.observations = Observations.find().fetch();
      }
      if(typeof Procedures === "object"){
        data.ccd.procedures = Procedures.find().fetch();
      }  


      var filters = get(Meteor.user(), 'profile.filters');

      if(filters){
        if(filters.remove && filters.remove.length > 0){
          data.query.$nor = [];
          filters.remove.forEach(function(term){
            if(term){
              data.query.$nor.push({ "code.text": { "$regex": "^" + term } })
            }
          });  
        }
  
        if(filters.mustHave && filters.mustHave.length > 0){
          data.query.$or = [];
          filters.mustHave.forEach(function(term){
            if(term){
              data.query.$or.push({ "code.text": { "$regex": "^" + term } })
            }
          })          
        }
      }
    }

    console.log('ContinuityOfCarePage[data]', data);
    return data;
  }
  openLink(url){
    browserHistory.push(url);
  }
  openFastImport(){
    browserHistory.push('/fast-import-chart')
  }
  deduplicate(collectionName){
    console.log('deduplicate', collectionName)    
  }  
  importHealthRecord(){
    MedicalRecordImporter.healthRecord();
  }
  render(){

    var allergiesCard;
    var carePlansCard;
    var conditionsCard;
    var diagnosticReportsCard;
    var immunizationsCard;
    var medicationsCard;
    var medicationOrdersCard;
    var medicationStatementsCard;
    var observationsCard;
    var proceduresCard;
    var patientsCard;

    var chartIsEmpty = true;

    if(Patients.find().count() > 0){
      chartIsEmpty = false;
      patientsCard = <div>
        <GlassCard >
          <CardTitle title="Patients" />
          <CardText>
            <PatientsTable
              data={ this.data.ccd.patients } 
            />            
          </CardText>
          <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/patients') } />
          </CardActions>
        </GlassCard> 
        <DynamicSpacer />
      </div>      
    }


    if(AllergyIntolerances.find().count() > 0){
      chartIsEmpty = false;
      allergiesCard = <div>
        <GlassCard>
          <CardTitle title="Allergies" />
          <CardText>
            <AllergyIntolerancesTable
              data={ this.data.ccd.allergyIntolerances } 
              displayDates={true} 
            />
        </CardText>
          <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/allergies') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'Allergies') } />
          </CardActions>
        </GlassCard>   
        <DynamicSpacer />
      </div>
    }

    if(CarePlans.find().count() > 0){
      chartIsEmpty = false;
      carePlansCard = <div>
        <GlassCard>
          <CardTitle title="CarePlans" />
          <CardText>
            <CarePlansTable
              data={ this.data.ccd.carePlans } 
              displayDates={true} 
            />                  
          </CardText>
          <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/care-plans') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'CarePlans') } />
          </CardActions>
        </GlassCard>
        <DynamicSpacer />
      </div>
    }
    if(Conditions.find().count() > 0){
      chartIsEmpty = false;
      conditionsCard = <div>
        <GlassCard>
          <CardTitle title="Conditions" />
          <CardText>
            <ConditionsTable
              data={ this.data.ccd.conditions } 
              displayDates={true} 
            />                  
          </CardText>
          <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/conditions') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'Conditions') } />
          </CardActions>
        </GlassCard>
        <DynamicSpacer />
      </div>
    }
    if(DiagnosticReports.find().count() > 0){
      chartIsEmpty = false;
      diagnosticReportsCard = <div>
        <GlassCard>
          <CardTitle title="Diagnostic Reports" />
          <CardText>
            <DiagnosticReportsTable 
              data={ this.data.ccd.diagnosticReports } 
              displayDates={true} 
            />
            
          </CardText>
          <CardActions>       
          <FlatButton label='Add' onClick={ this.openLink.bind(this, '/diagnostic-reports') } />
          <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'DiagnosticReports') } />
          </CardActions>
        </GlassCard>  
        <DynamicSpacer />
      </div>
    }
    if(Immunizations.find().count() > 0){
      chartIsEmpty = false;
      immunizationsCard = <div>
        <GlassCard>
          <CardTitle title="Immunizations" />
          <CardText>
            <ImmunizationsTable 
              data={ this.data.ccd.immunizations } 
              displayDates={true} 
            />
          </CardText>
          <CardActions>     
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/immunizations') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'Immunizations') } />
          </CardActions>
        </GlassCard>
        <DynamicSpacer />
      </div>
    }
    // if(Medications.find().count() > 0){
    //   medicationsCard = 
    // }
    if(MedicationOrders.find().count() > 0){
      chartIsEmpty = false;
      medicationOrdersCard = <div>
        <GlassCard>
          <CardTitle title="Medication Orders" />
          <CardText>
            <MedicationOrdersTable
              data={ this.data.ccd.medicationOrders } 
              displayDates={true} 
            />
            
          </CardText>
          <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/medications-orders') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'MedicationOrders') } />
          </CardActions>
        </GlassCard>          
        <DynamicSpacer />
      </div>      
    }
    if(MedicationStatements.find().count() > 0){
      chartIsEmpty = false;
      medicationStatementsCard = <div>
        <GlassCard>
          <CardTitle title="Medication Statements" />
          <CardText>
            <MedicationStatementsTable
              data={ this.data.ccd.medicationStatements } 
              displayDates={true} 
            />
            
          </CardText>f
          <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/medication-statements') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'MedicationStatements') } />
          </CardActions>
        </GlassCard>
        <DynamicSpacer />
      </div>
    }
    if(Observations.find().count() > 0){
      chartIsEmpty = false;
      observationsCard = <div>
        <GlassCard>
          <CardTitle title="Observations" />
          <CardText>
            <ObservationsTable
              //data={ this.data.ccd.observations } 
              barcodes={false}
              showValueString={false}
              showComparator={false}
              multiline={true}
              query={this.data.query}
            />
            
          </CardText>
          <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/observations') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'Observations') } />
          </CardActions>
        </GlassCard>
        <DynamicSpacer />
      </div>        
    }
    if(Procedures.find().count() > 0){
      chartIsEmpty = false;
      proceduresCard = <div>
        <GlassCard>
          <CardTitle title="Procedures" />
          <CardText>
            <ProceduresTable
              data={ this.data.ccd.procedures } 
              displayDates={true} 
            />
            
          </CardText>
          <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/procedures') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'Procedures') } />
          </CardActions>
        </GlassCard> 
        <DynamicSpacer />
      </div>      
    }
    

    var importWidget;
    var chart;
    var importStyle = {
      textAlign: 'center',
      position: 'relative',
      top: ((Session.get('appHeight') - 128 ) * 0.5) - 120 + 'px'
    }

    if(chartIsEmpty){
      if(Meteor.isCordova){
        if(['iPhone'].includes(window.navigator.platform)){
          importWidget = <Grid>
            <Row style={importStyle}>
              <h1 style={{fontWeight: '200'}}>No health data found.</h1>
              <RaisedButton label="Import Apple HealthRecords" onClick={ this.importHealthRecord } primary={true} />
            </Row>
          </Grid>  
        }
      } else {
        importWidget = <Grid>
        <Row style={importStyle}>
          <h1 style={{fontWeight: '200'}}>No health data found.</h1>
          <RaisedButton label="Import Medical Charts" onClick={this.openFastImport } primary={true} />
        </Row>
      </Grid>  
      }
    } else {
      chart = <div>
      { allergiesCard }
      { carePlansCard }                        
      { conditionsCard }                        
      { diagnosticReportsCard }
      { immunizationsCard }
      { medicationOrdersCard }
      { observationsCard }
      { patientsCard }
      { proceduresCard }
      </div>
    }

    return(
      <div id="ContinuityOfCarePage">
        <FullPageCanvas >
          <Grid>
            <Row>
              <Col>
                { importWidget }
                { chart }
              </Col>
            </Row>
          </Grid>
        </FullPageCanvas>
      </div>
    );
  }
}
ReactMixin(ContinuityOfCarePage.prototype, ReactMeteorData);
export default ContinuityOfCarePage;