
import { Col, Row } from 'react-bootstrap';
import { CardText, CardTitle, CardActions, Tab, Tabs, FlatButton, TextField, RaisedButton, Toggle } from 'material-ui';
import React, {Component} from 'react'
import { get } from 'lodash';
import { ReactMeteorData } from 'meteor/react-meteor-data';
import ReactMixin from 'react-mixin';

import { GlassCard, FullPageCanvas, DynamicSpacer } from 'meteor/clinical:glass-ui';

import Chip from '@material-ui/core/Chip';



const chipStyles = theme => ({
  root: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
  },
  chip: {
    margin: theme.spacing.unit,
  },
});


Session.setDefault('formDesignerTabIndex', 0);

Session.setDefault('uteroToggle', true);
Session.setDefault('pubertyToggle', true);
Session.setDefault('menopauseToggle', true);
Session.setDefault('hospiceToggle', true);
Session.setDefault('riskAssessmentToggle', true);

export class FiltersPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      removeFilterInput: '',
      mustHaveInput: ''
    }
  }

  getMeteorData() {
    let data = {
      style: {
        tab: {
          borderBottom: '1px solid lightgray',
          borderRight: 'none',
          width: '200px'
        }
      },
      tabIndex: Session.get('formDesignerTabIndex'),
      ccd: null,
      timelineEntries: [],
      resourceTypes: [],
      toggles: {
        uteroToggled: Session.get('uteroToggle'),
        pubertyToggled: Session.get('pubertyToggle'),
        menopauseToggled: Session.get('menopauseToggle'),
        hospiceToggled: Session.get('hospiceToggle'),
        riskAssessmentToggled: Session.get('riskAssessmentToggle')
      }
    }

    if(Meteor.user()){
      data.resourceTypes = get(Meteor.user(), 'profile.filters.resourceTypes', []);
      data.filters = get(Meteor.user(), 'profile.filters');
    }

    if(process.env.NODE_ENV === "test") console.log("FiltersPage[data]", data);
    return data;
  }; 

  setToggleState(collection, isInputChecked){
    let toggleStates = Session.get('toggleStates');
    toggleStates[collection] = isInputChecked;
    console.log('toggleStates', toggleStates)
    Session.set('toggleStates', toggleStates);
  }

  toggleAllergies(event, isInputChecked){    
    this.setToggleState('allergies', isInputChecked)
  }
  toggleCarePlans(event, isInputChecked){    
    this.setToggleState('careplans', isInputChecked)
  }
  toggleConditions(event, isInputChecked){    
    this.setToggleState('conditions', isInputChecked)
  }
  toggleImmunizations(event, isInputChecked){
    this.setToggleState('immunizations', isInputChecked)
  }
  toggleMedications(event, isInputChecked){    
    this.setToggleState('medications', isInputChecked)
  }
  toggleMedicationStatements(event, isInputChecked){    
    this.setToggleState('medications', isInputChecked)
  }
  toggleMedicationOrders(event, isInputChecked){    
    this.setToggleState('medications', isInputChecked)
  }
  toggleObservations(event, isInputChecked){    
    this.setToggleState('observations', isInputChecked)
  }
  toggleProcedures(event, isInputChecked){    
    this.setToggleState('procedures', isInputChecked)
  }


  handleTabChange(index){
    Session.set('formDesignerTabIndex', index);
  }
  handleDelete(){
    alert('handleDelete')
  }
  saveRemoveFilter(){
    Meteor.users.update({_id: Meteor.userId()}, {$addToSet: {
      'profile.filters.remove': this.state.removeFilterInput
    }})
    this.setState({
      removeFilterInput: ""
    })
  }
  saveMustUseFilter(){
    Meteor.users.update({_id: Meteor.userId()}, {$addToSet: {
      'profile.filters.mustHave': this.state.mustHaveInput
    }})
    this.setState({
      mustHaveInput: ""
    })

  }
  handleAddRemoveFilter(proxy, value){
    //console.log('handleAddRemoveFilter', value)
    this.setState({
      removeFilterInput: value
    })
  }
  handleAddMustUseFilter(proxy, value){
    //console.log('handleAddMustUseFilter', value)
    this.setState({
      mustHaveInput: value
    })
  }
  handleDeleteRemove(value, proxy, foo){
    //console.log('handleDeleteRemove', value)

    var newTags = [];
    var currentTags = get(Meteor.user(), 'profile.filters.remove');

    currentTags.forEach(function(tag){
      if(tag !== value){
        newTags.push(tag);
      }
    })

    //console.log('newTags', newTags)

    Meteor.users.update({_id: Meteor.userId()}, {$set: {
      'profile.filters.remove': newTags
    }})
  }
  handleDeleteMustHave(value, proxy, foo){
    //console.log('handleDeleteRemove', value, proxy, foo)

    var newTags = [];
    var currentTags = get(Meteor.user(), 'profile.filters.mustHave');

    currentTags.forEach(function(tag){
      if(tag !== value){
        newTags.push(tag);
      }
    })


    //console.log('newTags', newTags)

    Meteor.users.update({_id: Meteor.userId()}, {$set: {
      'profile.filters.mustHave': newTags
    }})

  }
  toggleFilterInProfile(collectionName){
    var resourceTypes = [];
    resourceTypes = get(Meteor.user(), 'profile.filters.resourceTypes');
    console.log('toggleFilterInProfile.resourceTypes', resourceTypes)    
    console.log('typeof toggleFilterInProfile', typeof resourceTypes)    
    
    if(typeof resourceTypes === "object"){
      if(resourceTypes.includes(collectionName)){
        Meteor.users.update({_id: Meteor.userId()}, {$pull: {
          'profile.filters.resourceTypes': collectionName
        }})
      } else {
        Meteor.users.update({_id: Meteor.userId()}, {$addToSet: {
          'profile.filters.resourceTypes': collectionName        
        }})     
      }
    } else {
      console.log('Profile doesnt have filters set.  Maybe add this into Accounts.js?')

      Meteor.users.update({_id: Meteor.userId()}, {$set: {
        'profile.filters.resourceTypes': [
          collectionName
        ]
      }});

      // console.log('Meteor.user()', Meteor.user());
      // console.log('stringify(Meteor.user())', JSON.stringify(Meteor.user()));
    }
  }
  toggleProcedures(){
    this.toggleFilterInProfile('Procedures')
  }
  toggleObservations(){
    this.toggleFilterInProfile('Observations')
  }
  toggleMedicationStatements(){
    this.toggleFilterInProfile('MedicationStatements')
  }
  toggleMedicationOrders(){
    this.toggleFilterInProfile('MedicationOrders')
  }
  toggleImmunizations(){
    this.toggleFilterInProfile('Immunizations')
  }
  toggleConditions(){
    this.toggleFilterInProfile('Conditions')
  }
  toggleCarePlans(){
    this.toggleFilterInProfile('CarePlans')
  }
  toggleAllergies(){
    this.toggleFilterInProfile('Allergies')
  }
  togglePatients(){
    this.toggleFilterInProfile('Patients')
  }


  toggleUtero(){
    console.log('toggleUtero')
    Session.toggle('uteroToggle');
  }
  togglePuberty(){
    Session.toggle('pubertyToggle');
  }
  toggleMenopause(){
    Session.toggle('menopauseToggle');
  }
  toggleHospice(){
    Session.toggle('hospiceToggle');
  }
  toggleRiskAssessment(){
    Session.toggle('riskAssessmentToggle');
  }

  render(){
    let self = this;

    var removeChips = [];
    if(get(this, 'data.filters.remove')){
      this.data.filters.remove.forEach(function(filter){
        removeChips.push(<Chip 
                            label={filter} 
                            key={filter} 
                            className={chipStyles.chip} 
                            variant="outlined" 
                            style={{marginRight: '10px', fontWeight: 'bold', fontSize: '120%'}}
                            clickable
                            onDelete={ self.handleDeleteRemove.bind(this, filter) } />)
      })
    }
    
    
    var mustHaveChips = [];
    if(get(this, 'data.filters.mustHave')){
      this.data.filters.mustHave.forEach(function(filter){
        mustHaveChips.push(<Chip 
                            label={filter} 
                            key={filter} 
                            className={chipStyles.chip} 
                            variant="outlined" 
                            style={{marginRight: '10px', fontWeight: 'bold', fontSize: '120%'}}
                            onDelete={ self.handleDeleteMustHave.bind(this, filter) } />)
      })
    }

    var resourceTypes = this.data.resourceTypes;
    console.log('this.data.resourceTypes', this.data.resourceTypes);
    console.log('typeof this.data.resourceTypes', typeof this.data.resourceTypes);

    let allergiesToggled = false;
    let carePlansToggled = false;
    let conditionsToggled = false;
    let immunizationsToggled = false;
    let medicationOrdersToggled = false;
    let medicationStatementsToggled = false;
    let observationsToggled = false;
    let patientsToggled = false;
    let proceduresToggled = false;

    if(typeof resourceTypes === "object"){
      allergiesToggled = resourceTypes.includes('Allergies');
      carePlansToggled = resourceTypes.includes('CarePlans');
      conditionsToggled = resourceTypes.includes('Conditions');
      immunizationsToggled = resourceTypes.includes('Immunizations');
      medicationOrdersToggled = resourceTypes.includes('MedicationOrders');
      medicationStatementsToggled = resourceTypes.includes('MedicationStatements');
      observationsToggled = resourceTypes.includes('Observations');
      patientsToggled = resourceTypes.includes('Patients');  
      proceduresToggled = resourceTypes.includes('Procedures');  
    }
    
    return(
      <div id="FiltersPage">
        <FullPageCanvas >
          <Row>
            <Col md={6}>
              <GlassCard>
                <CardTitle title='Remove Filters'  />
                <CardText>
                  <FlatButton 
                    label="Save" 
                    onClick={ this.saveRemoveFilter.bind(this) } 
                    primary={true}
                    style={{position: 'absolute',
                      right: '30px',
                      marginTop: '30px',
                      zIndex: 1000
                    }}
                    />
                  <TextField
                    id='addRemoveFilterInput'
                    name='addRemoveFilterInput'
                    type='text'
                    floatingLabelText="Don't include these words"
                    floatingLabelFixed={true}
                    value={this.state.removeFilterInput}
                    onChange={ this.handleAddRemoveFilter.bind(this) }
                    hintText={ 'Depression'}
                    fullWidth
                    />

                  <br/>

                  { removeChips }


                </CardText>
              </GlassCard>
              <DynamicSpacer />              
              <GlassCard>
                <CardTitle title='Must Have Requirements'  />
                <CardText>
                  <FlatButton 
                    label="Save" 
                    onClick={ this.saveMustUseFilter.bind(this) } 
                    primary={true}
                    style={{position: 'absolute',
                      right: '30px',
                      marginTop: '30px',
                      zIndex: 1000
                    }}
                    />

                  <TextField
                    id='mustHaveInput'
                    name='mustHaveInput'
                    type='text'
                    floatingLabelText='Must Use'
                    onChange={ this.handleAddMustUseFilter.bind(this) }
                    value={this.state.mustHaveInput}
                    floatingLabelFixed={true}
                    hintText={ 'Weight'}
                    fullWidth
                    />
                    

                  { mustHaveChips }

                </CardText>
              </GlassCard>


            <DynamicSpacer />              
              <Row>
                <Col md={6} >
                  <GlassCard>
                    <CardTitle title='Resource Types'  />
                    <CardText>
                      <Toggle 
                        label="Allergies"
                        labelPosition='right'
                        onToggle={this.toggleAllergies.bind(this)} 
                        toggled={ allergiesToggled }  
                      /><br />
                      <Toggle 
                        label="CarePlans"
                        labelPosition='right'
                        onToggle={this.toggleCarePlans.bind(this)} 
                        toggled={ carePlansToggled }  
                      /><br />
                      <Toggle 
                        label="Conditions"
                        labelPosition='right'
                        onToggle={this.toggleConditions.bind(this)} 
                        toggled={ conditionsToggled }  
                      /><br />
                      <Toggle 
                        label="Immunizations"
                        labelPosition='right'
                        onToggle={this.toggleImmunizations.bind(this)} 
                        toggled={ immunizationsToggled }  
                      /><br />
                      <Toggle 
                        label="MedicationOrders"
                        labelPosition='right'
                        onToggle={this.toggleMedicationOrders.bind(this)} 
                        toggled={ medicationOrdersToggled }  
                      /><br />                                                      
                      <Toggle 
                        label="MedicationStatements"
                        labelPosition='right'
                        onToggle={this.toggleMedicationStatements.bind(this)} 
                        toggled={ medicationStatementsToggled }  
                      /><br />                  
                      <Toggle 
                        label="Observations"
                        labelPosition='right'
                        onToggle={this.toggleObservations.bind(this)} 
                        toggled={ observationsToggled }  
                      /><br />                  
                      <Toggle 
                        label="Patients"
                        labelPosition='right'
                        onToggle={this.togglePatients.bind(this)} 
                        toggled={ patientsToggled }  
                      /><br />                  
                      <Toggle 
                        label="Procedures"
                        labelPosition='right'
                        onToggle={this.toggleProcedures.bind(this)} 
                        toggled={ proceduresToggled }  
                      /><br />                  

                    </CardText>
                  </GlassCard>
                  <DynamicSpacer />
                </Col>
                <Col md={6} >
                  <GlassCard>
                    <CardTitle title='Timeline Features' disabled />
                    <CardText disabled >
                      <Toggle 
                        label="in Utero"
                        labelPosition='right'
                        onToggle={this.toggleUtero.bind(this)} 
                        toggled={ this.data.toggles.uteroToggled }  
                        disabled={ true }
                      /><br />
                      <Toggle 
                        label="Puberty"
                        labelPosition='right'
                        onToggle={this.togglePuberty.bind(this)} 
                        toggled={ this.data.toggles.pubertyToggled }  
                        disabled={ true }
                      /><br />
                      <Toggle 
                        label="Menopause"
                        labelPosition='right'
                        onToggle={this.toggleMenopause.bind(this)} 
                        toggled={ this.data.toggles.menopauseToggled }  
                        disabled={ true }
                      /><br />
                      <Toggle 
                        label="Hospice"
                        labelPosition='right'
                        onToggle={this.toggleHospice.bind(this)} 
                        toggled={ this.data.toggles.hospiceToggled }  
                        disabled={ true }
                      /><br />
                      <Toggle 
                        label="Risk Assessment"
                        labelPosition='right'
                        onToggle={this.toggleRiskAssessment.bind(this)} 
                        toggled={ this.data.toggles.riskAssessmentToggled }  
                        disabled={ true }
                      /><br />                                                      



                    </CardText>
                  </GlassCard>
                </Col>
              </Row>
            </Col>

            <Col md={6}>

            </Col>
          </Row>
        </FullPageCanvas>
      </div>
    );
  }
}
ReactMixin(FiltersPage.prototype, ReactMeteorData);
export default FiltersPage;


