

import { CardHeader, CardText, CardTitle } from 'material-ui/Card';
import React, {Component} from 'react'
import { get, has, sortBy } from 'lodash';

import { ReactMeteorData } from 'meteor/react-meteor-data';
import ReactMixin from 'react-mixin';

import { Glass, GlassCard, VerticalCanvas, DynamicSpacer } from 'meteor/clinical:glass-ui';
import { browserHistory } from 'react-router';

export class SupportPage extends React.Component {
  constructor(props) {
    super(props);
  }

  getMeteorData() {
    let data = {
      ccd: null,
      timelineEntries: []
    }

    if(process.env.NODE_ENV === "test") console.log("SupportPage[data]", data);
    return data;
  };
  openLink(url){
    window.location.replace(url);
  }
  render(){
    

    return(
      <div id="SupportPage">
        <VerticalCanvas >
          <GlassCard onClick={this.openLink.bind(this, 'mailto:support@symptomatic.io')} style={{cursor: 'pointer'}}>
            <CardTitle               
              title='support@symptomatic.io' 
              />
          </GlassCard>
          <DynamicSpacer />

          <GlassCard onClick={this.openLink.bind(this, 'https://groups.google.com/a/symptomatic.io/forum/#!forum/support')} style={{cursor: 'pointer'}}>
            <CardTitle 
              href='https://groups.google.com/a/symptomatic.io/forum/#!forum/support'
              title='Google Group' 
              subtitle='Email thread and public discussion group for using Symptomatic.' 
              />
          </GlassCard>
          <DynamicSpacer />

          <GlassCard onClick={this.openLink.bind(this, 'https://www.linkedin.com/groups/12150525/')} style={{cursor: 'pointer'}}>
            <CardTitle 
                title='LinkedIn FHIR Users Group' 
                subtitle='Professional interest group of clinicians and informaticists using FHIR.'
                />
          </GlassCard>


        </VerticalCanvas>
      </div>
    );
  }
}
ReactMixin(SupportPage.prototype, ReactMeteorData);
export default SupportPage;

