  clearLocalCache(){
    if(confirm("Are you absolutely sure?")){

      var resourceTypes = [
        'AllergyIntolerances',
        'CarePlans',
        'Conditions',
        'Consents',
        'Contracts',
        'ClinicalImpressions',
        'Devices',
        'DiagnosticReports',
        'Goals',
        'Immunizations',
        'ImagingStudies',
        'Locations',
        'Medications',
        'MedicationOrders',
        'MedicationStatements',
        'Organizations',
        'Observations',
        'Patients',
        'Practitioners',
        'Persons',
        'Procedures',
        'Questionnaires',
        'QuestionnaireResponses',
        'RiskAssessments',
        'RelatedPersons',
        'Sequences'
      ];

      resourceTypes.forEach(function(resourceType){
        if(Mongo.Collection.get(resourceType)){
          Mongo.Collection.get(resourceType).find().forEach(function(record){
            Mongo.Collection.get(resourceType).remove({_id: record._id})
            Mongo.Collection.get(resourceType)._collection.remove({_id: record._id})
          })
        }
      })

      Meteor.call('getServerStats', function(error, result){
        if(result){
          Session.set('datalakeStats', result);
        }
      });
    }
  }
